.search {
  display: flex;
  align-items: center;

  img {
    height: 32px;
    width: 32px;
    filter: brightness(0.75);
  }

  input {
    color: #FFFFFF;
    font-size: 24px;
    font-family: Rubik-Light, sans-serif;
    outline: none;
    border: none;
    height: 40px;
    width: 100%;
    margin-right: 16px;
    margin-left: 16px;
    background-color: #0F1215;

    &::placeholder {
      color: #A9ABAD;
    }
  }

  img.cross {
    cursor: pointer;
  }
}

/*Media*/

@media (max-width: 767px) {
  .search {
    input {
      margin-left: 10px;
      font-size: 18px;
      line-height: 22px;
      margin-top: 1px;
    }

    img {
      width: 20px;
      height: 20px;
    }
  }
}