.item {
  cursor: pointer;

  .type_of_lesson {
    margin-top: 1px;
    display: flex;

    div {
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background-color: #6C63FF;
    }

    div:not(:last-child) {
      margin-right: 1px;
    }
  }
}

.prevOrNextMonth {
  opacity: 0.3;
}

.hidden {
  visibility: hidden;
}

.number_of_week {
  margin-top: 6px;
  width: 53px;
  height: 53px;
  border-radius: 50%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;

  p {
    color: #A9ABAD;
  }

  p:nth-child(2), p:nth-child(3) {
    visibility: hidden;
    position: absolute;
  }
}

.number_of_week.today p, .number_of_week.selected p {
  color: #1B1E21;
}

.number_of_week.today {
  background-color: #A9ABAD;
}


.number_of_week.selected {
  background-color: #12DE94;
}

.day_of_week p, .number_of_week p {
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

/*Media*/

@media (max-width: 767px) {
  .item {
    .number_of_week {
      width: 34px;
      height: 34px;

      p {
        font-size: 12px;
        line-height: 14px;
      }
    }

    .type_of_lesson {
      div {
        width: 3px;
        height: 3px;
      }
    }
  }
}
