.lessons_item {
  padding: 23px 40px;
  background-color: #1B1E21;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.35);
  border-radius: 25px;
  border: 2px solid rgba(108, 99, 255, 0.3);

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  p {
    font-size: 20px;
    line-height: 24px;
  }

  .lessons_item_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .type_of_lesson {
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      width: 60px;
      height: 60px;
      background-color: #6C63FF;
    }

    .teacher_lesson {
      display: flex;
      flex-direction: column;
    }

    .type_of_lesson.deActive {
      display: none;
    }
  }

  .lesson {
    font-size: 24px;
    line-height: 28px;
  }

  .teacher, .time p {
    color: #A9ABAD;
  }

  .deActive {
    display: none;
  }

  .active p {
    color: #FFFFFF;
  }

  .active {
    display: initial;
  }
}

/*Media*/

@media (max-width: 1023px) {
  .lessons_item {
    padding: 23px 20px;

    .lessons_item_wrapper {
      .type_of_lesson {
        display: none;
      }
    }
  }
}

@media (max-width: 767px) {
  .lessons_item {
    padding: 24px 20px;
    border-radius: 15px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  .lessons_item_wrapper {

    .lesson {
      font-size: 12px;
      line-height: 14px;
    }

    div {
      p {
        font-size: 10px;
        line-height: 12px;
      }
    }
  }
}

@media (max-width: 419px) {
  .lessons_item {
    padding: 13px 10px;
    border-radius: 15px;
  }
}

