.week_wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
  margin-left: 50px;
  margin-right: 50px;

  .parity {
    p {
      font-size: 18px;
      line-height: 21px;
      font-family: Rubik-Light, sans-serif;
    }

    .parity_color {
      color: #12DE94;
    }

    .noparity_color {
      color: #F36855;
    }
  }
}

.calendar {
  width: calc(100% - 100px);
  margin-top: 40px;
  padding-bottom: 25px;
  margin-left: 50px;

  tbody {

    tr {
      display: flex;
      justify-content: space-between;
    }

    th {
      font-size: 24px;
      line-height: 28px;
      width: 53px;

      p {
        text-transform: uppercase;
        color: #A9ABAD;
      }

      &.selected {
        p {
          color: #FFFFFF;
        }
      }
    }
  }
}

/*Media*/

@media (max-width: 1919px) {
  .week_wrap {
    padding-top: 30px;
  }
  .calendar {
    margin-top: 30px;
  }
}

@media (max-width: 1023px) {
  .calendar {
    margin-left: 20px;
    width: calc(100% - 40px);
  }
  .week_wrap {
    margin-left: 20px;
    margin-right: 20px;
    padding-top: 20px;
  }
}

@media (max-width: 767px) {
  .calendar {
    margin-left: 10px;
    width: calc(100% - 20px);
    margin-top: 20px;
    padding-bottom: 20px;

    tbody {
      th {
        font-size: 12px;
        line-height: 14px;
        width: 34px;
      }
    }
  }
  .week_wrap {
    margin-left: 10px;
    margin-right: 10px;
    padding-top: 10px;

    .parity p {
      font-size: 10px;
      line-height: 12px;
    }
  }
}