.date_wrap {
  align-items: center;

  .date {
    font-size: 32px;
    line-height: 38px;
    justify-self: center;
    width: max-content;
  }

  .left_arrow {
    margin-right: 20px;
    display: none;
    cursor: pointer;
    justify-self: start;
  }

  .right_arrow {
    margin-left: 20px;
    display: none;
    cursor: pointer;
    justify-self: flex-start;
  }

  .button {
    margin-left: 60px;
    display: none;
    cursor: pointer;
    justify-self: start;

    p {
      padding: 6px 14px 6px 14px;
      border: 1px solid #A9ABAD;
      border-radius: 15px;
      font-size: 18px;
      line-height: 21px;
      width: 134px;
      text-align: center;
    }
  }

  .active {
    display: initial;
  }
}

.date_wrap.spec {
  display: grid;
  grid-template-columns: repeat(4, min-content);

  p.date {
    text-align: center;
    width: 260px;
  }
}

/*Media*/

@media (max-width: 767px) {
  .date_wrap {
    .date {
      font-size: 14px;
      line-height: 17px;
    }

    .button {
      margin-left: 20px;

      p {
        padding: 6px 7px 6px 7px;
        font-size: 10px;
        line-height: 12px;
        width: 60px;
      }
    }

    .right_arrow, .left_arrow {
      width: 5px;
      height: auto;
    }

    .right_arrow {
      margin-left: 10px;
    }

    .left_arrow {
      margin-right: 10px;
    }
  }
  .date_wrap.spec {
    p.date {
      width: 130px;
    }
  }
}

@media (max-width: 419px) {
  .date_wrap.spec {
    p.date {
      width: 116px;
    }
  }
  .right_arrow {
    margin-left: 5px;
  }

  .left_arrow {
    margin-right: 5px;
  }

  .date_wrap {
    .button {
      margin-left: 10px;
      margin-right: 10px;
    }
  }
}