@import "./assets/fonts/rubik/rubik-font.css";

body {
  background: #15181B;
  overflow: hidden;
}

* {
  padding: 0;
  margin: 0;
  font-weight: normal;
  font-family: Rubik-Regular, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
  text-decoration: none;
  display: flex;
  align-items: center;
}

img {
  vertical-align: middle;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
  color: #FFFFFF;
}

p, th, td {
  margin-bottom: 0;
  color: #FFFFFF;
}

ul {
  margin-bottom: 0;
  list-style: none;
}