.grid {
  display: grid;
  grid-template-areas:  "pagename . search . . .";
  grid-template-columns: 482px minmax(30px, 1fr) 895px minmax(30px, 1fr) 432px minmax(50px, auto);
}

.page_name {
  grid-area: pagename;
  margin-top: 28px;
  margin-bottom: 26px;
  margin-left: 50px;
  display: flex;
  align-items: center;

  img {
    display: none;
    height: 32px;
    width: 32px;
    cursor: pointer;
  }

  h1 {
    font-size: 32px;
    line-height: 38px;
  }
}

.search {
  grid-area: search;
}

/*Media*/

@media (max-width: 1919px) {
  .grid {
    grid-template-areas:  "pagename . search .";
    grid-template-columns: 462px 1fr 1078px minmax(30px, auto);
  }
  .page_name {
    margin-left: 30px;

    h1 {
      margin-left: 15px;
    }

    img {
      display: initial;
    }
  }
}

@media (max-width: 1568px) {
  .grid {
    grid-template-areas:  "pagename search .";
    grid-template-columns: 462px 1fr minmax(30px, auto);
  }
}

@media (max-width: 1279px) {
  .grid {
    grid-template-columns: 330px 1fr minmax(30px, auto);
  }
}

@media (max-width: 1023px) {
  .grid {
    grid-template-columns: 310px 1fr minmax(20px, auto);
  }
  .page_name {
    margin-left: 20px;
  }
}

@media (max-width: 767px) {
  .grid {
    grid-template-columns: 134px 1fr minmax(10px, auto);
  }
  .page_name {
    margin-left: 10px;
    margin-top: 18px;
    margin-bottom: 18px;

    img {
      height: 20px;
      width: 20px;
    }

    h1 {
      font-size: 20px;
      line-height: 24px;
    }
  }
}