.bcg {
  background-color: #0F1215;
}

.nav {
  margin-top: 94px;
  height: 100%;
  z-index: 999;
}

.right_aside, .content, .nav, .header {
  position: fixed;
  top: 0;
}

.nav, .header {
  left: 0;
}

.header {
  width: 100%;
}

.right_aside {
  right: 0;
}

.content {
  left: 0;
  right: 0;
}

/*Media*/

@media (max-width: 1919px) {
  .right_aside {
    right: initial;
    left: 0;
  }
}

@media (max-width: 767px) {
  .nav {
    right: 0;
    margin-top: 62px;
  }
}


