.wrap_main {
  margin-top: 120px;
  margin-left: auto;
  margin-right: auto;
  width: 895px;
  background-color: #16191C;
  border-radius: 50px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);

  .wrap_first {
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
  }

  .wrap_second {
    margin-top: 2px;
    border-bottom-right-radius: 50px;
    border-bottom-left-radius: 50px;
    overflow-y: scroll;

    &::-webkit-scrollbar {
      width: 0;
    }
  }

  .wrap_first, .wrap_second {
    background-color: #1B1E21;
  }
}

.wrap_right_aside {
  margin-top: 120px;
  margin-right: 50px;
  width: 432px;
  background-color: #1B1E21;
  border-radius: 50px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);

  .events_wrap {
    margin: 47px 40px;

    .title {
      p {
        font-size: 32px;
        line-height: 38px;
      }
    }

    .events {
      margin-left: 13px;

      .event {
        display: grid;
        align-items: center;
        justify-content: flex-start;
        grid-template-areas: "dayevent namedateevent";
        grid-template-columns: 80px auto;
        margin-top: 20px;

        .day {
          grid-area: dayevent;

          p {
            font-size: 20px;
            line-height: 24px;
            color: #F36B55;
          }
        }

        .name_date {
          grid-area: namedateevent;
          margin-left: 42px;

          .name_of_event, .date_to_event {
            font-family: Rubik-Light, sans-serif;
          }

          .name_of_event {
            font-size: 20px;
            line-height: 24px;
          }

          .date_to_event {
            font-size: 16px;
            line-height: 19px;
            color: #A9ABAD;
          }
        }
      }
    }
  }
}

/*Media*/

@media (max-width: 1919px) {
  .wrap_main {
    margin-right: 30px;
    width: 1078px;

  }
  .wrap_right_aside {
    margin-right: 0;
    margin-left: 30px;
  }
}

@media (max-width: 1599px) {
  .wrap_right_aside {
    display: none;
  }
  .wrap_main {
    width: calc(100% - 60px);
    margin-right: auto;
  }
}

@media (max-width: 1023px) {
  .wrap_main {
    margin-top: 110px;
    width: calc(100% - 40px);
    border-radius: 30px;

    .wrap_first {
      border-top-left-radius: 30px;
      border-top-right-radius: 30px;
    }

    .wrap_second {
      border-bottom-right-radius: 30px;
      border-bottom-left-radius: 30px;
    }
  }
}

@media (max-width: 767px) {
  .wrap_main {
    margin-top: 75px;
    width: calc(100% - 30px);
    border-radius: 15px;

    .wrap_first {
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
    }

    .wrap_second {
      border-bottom-right-radius: 15px;
      border-bottom-left-radius: 15px;
    }
  }
}