.wrap {
  transform: translateX(0);
}

.active_nav {
  transform: translateX(0) !important;
}

.grid {
  display: grid;
  grid-template-areas:  "linkprofile"
                        "buttons";
  margin-left: 50px;
  padding-top: 28px;
  margin-right: 82px;
  grid-template-rows: min-content;
  align-items: start;
}

.link_profile {
  grid-area: linkprofile;
  display: flex;
  align-items: center;

  .wrap_info {
    margin-left: 20px;
  }

  .full_name {
    font-size: 24px;
    line-height: 28px;
  }

  .email {
    font-size: 20px;
    line-height: 24px;
    color: #A9ABAD;
    font-family: Rubik-Light, sans-serif;
    margin-top: 5px;
  }

  img {
    border-radius: 50%;
    height: 100px;
    width: 100px;
  }
}

.buttons {
  grid-area: buttons;
  margin-top: 70px;

  li {
    margin-bottom: 30px;
    display: flex;
    align-items: center;

    img {
      width: 32px;
      height: 32px;
      filter: brightness(0.75);
    }

    p {
      margin-left: 20px;
      font-size: 24px;
      line-height: 28px;
      color: #A9ABAD;
    }


    &:last-child {
      margin-top: 260px;
    }
  }

  .active p {
    color: #FFFFFF;
  }

  .active img {
    filter: brightness(2);
  }

  li:last-child {
    cursor: pointer;
    margin-top: calc(100vh - 696px);
  }
}

/*Media*/

@media (max-height: 726px) {
  .grid {
    overflow-y: scroll;

    &::-webkit-scrollbar {
      width: 0;
    }
  }
  .buttons {
    li:last-child {
      cursor: pointer;
      margin-top: 0;
    }
  }
}

@media (max-width: 1919px) {
  .grid {
    margin-right: 84px;
    margin-left: 30px;
  }
  .wrap {
    transform: translateX(-100%);
  }
}

@media (max-width: 1279px) {
  .grid {
    margin-right: 68px;
  }
  .buttons {
    li:last-child {
      margin-top: calc(100vh - 763px);
    }
  }

  .link_profile {
    flex-direction: column;
    align-items: flex-start;

    .wrap_info {
      margin-top: 10px;
      margin-left: 0;
    }
  }
}

@media (max-width: 1279px) and (max-height: 792px) {
  .buttons {
    li:last-child {
      margin-top: 0;
    }

    margin-top: 40px;
  }
}

@media (max-width: 1023px) {
  .grid {
    margin-left: 20px;
    margin-right: 58px;
  }
}

@media (max-width: 767px) {
  .buttons {
    margin-top: 30px;
  }
  .grid {
    margin-right: 20px;
    margin-left: 10px;
  }
  .link_profile {
    flex-direction: row;
    align-items: center;

    img {
      height: 80px;
      width: 80px;
    }

    .wrap_info {
      margin-top: 0;
      margin-left: 10px;
    }

    .full_name {
      font-size: 24px;
      line-height: 28px;
    }

    .email {
      font-size: 20px;
      line-height: 24px;
    }
  }
}

@media (max-width: 419px) {
  .link_profile {
    flex-direction: column;
    align-items: flex-start;

    .wrap_info {
      margin-top: 10px;
      margin-left: 0;
    }
  }
}